//===========================================
// -Helper Classes start
//===========================================

.center {
  margin: auto;
}

// Search list Class

.search-list {
  box-shadow: 0 rem(10) rem(49) 0 rgba(0, 0, 0, 0.13);
  border-radius: rem(12);
  background-color: white;
  position: absolute;
  z-index: 1;
  //width: 100%;
  display: none;
  max-height: rem(150);
  overflow-y: auto;


  ul {
    margin-bottom: rem(0);
  }

  li {
    padding: rem(5) rem(17) rem(5) rem(17);

    &:first-child {
      padding-top: rem(10);
    }

    &:last-child {
      padding-bottom: rem(7);
    }

    &:hover {
      background-color: #f6f6f6;
      border-radius: rem(12);
    }
  }

  .search-list-items {

    display: flex;
    align-items: center;


    img {
      width: rem(25);
    }

    span {
      font-size: rem(10);
    }

    .name {
      font-weight: 600;
      padding-left: rem(6);
    }

    .email {
      margin-left: auto;

    }
  }
}


.profile-img-name {
  text-align: right;
  max-width: 130px;
  display: flex;
  justify-content: flex-end;
}

.profile-name-styling {
  font-size: 14px;
  padding: 0;
  margin: 0;
  font-weight: bold;
  color: #000;
}

.small-styling {
  font-size: 11px;
  margin: 0;
  padding: 0;
  color: #7a7e88;
}

.progress-padding {
  padding-top: 10px;
  // width: 100%;
}

.progress-color {
  background-color: #24cc09;

}

.progressBar {
  width: 100%;
}

.progress-hieght {
  height: 12px;
}

.progress-row {
  align-items: center;
}

.scrollbar-height {
  height: 180px;
  // overflow-x: scroll;
}

.checkbox-sizing {
  top: -54px;
  width: 96%;
  left: 9px;
}

.text-plan-cards {
  display: flex;
  flex-wrap: wrap;
}


// Search list Class End 


// Tooltip
// bootstrap tool tip

.tooltip-inner {
  //max-width: 200px;
  padding: 5px 8px;
  font-size: 10px;
  color: white;
  text-align: center;
  background-color: $sky-blue;
  border-radius: 6px;
  margin-right: 20px;
  border-color: $sky-blue;
  min-width: 60px;
  max-width: none;
  //margin-bottom: 10px;
  // width: auto;
  // max-width: 150px;
  // font-size: 100%;
  // white-space: nowrap;
  //position: relative;

  @include mq('lg', max) {
    margin-right: 40px;
  }
}

.bs-tooltip-auto[x-placement^=top] .arrow::before,
.bs-tooltip-top .arrow::before {
  //position: absolute;
  border-top-color: $sky-blue;
  margin-right: 15px;
  //top: -10px;
}

.bs-tooltip-auto[x-placement^=right] .arrow::before,
.bs-tooltip-right .arrow::before {
  border-right-color: transparent;
}

.bs-tooltip-auto[x-placement^=bottom] .arrow::before,
.bs-tooltip-bottom .arrow::before {
  border-bottom-color: transparent;
}

.bs-tooltip-auto[x-placement^=left] .arrow::before,
.bs-tooltip-left .arrow::before {
  border-left-color: transparent;
}


.valign-top {
  vertical-align: top;
}

.wd350 {
  min-width: 350px;
}

.error-msg {
  .input_field.required {
    background: rgba(231, 30, 30, 0.06);
    border: 2px solid #e71e1e;
  }

  .text {
    font-size: 13px;
    color: #e71e1e;
  }

  .icon-warning {
    color: #e71e1e;
  }
}

.radio-disc {
  display: block;
  font-size: 14px;
  color: #a4a4a4;
}

hr {
  &.radio-bdr {
    margin: 25px 40px;
  }
}

// Tooltip End 
//===========================================

// Card box 
.card-box {
  border: rem(2) solid white;
  background-color: white;
  border-radius: rem(20);
  padding: rem(15) rem(20);
  box-shadow: 0 rem(4) rem(6) 0 rgba(62, 73, 84, 0.04);
}

// -Helper Classes end
//===========================================