.milestone-inner-section {
    padding-top: 120px;
}

.page-option {
    padding: 20px 25px 16px 25px;
    border-top: 1px solid #ebebeb;
    text-align: right;
    a {
        color: $grey-300;
        &:hover{
            color: black;
        }
    }
    i {
        font-size: 23px;
    }
}

.sub-milestone-list-card {
    @include card-box;
    padding: 28px 28px;

    .sort-by {
        padding-top: 0px;
    }

    .content-row {
        .select-row {
            .creation-col {
                margin-right: 0px;
                margin-left: 0px;
            }
        }

        .select-row-list {
            &.without-icon {
                .custom-checkbox label::before {
                    top: 6px;
                }
                .custom-checkbox label::after {
                    top: 6px;
                }
            }

            li {
                box-shadow: 0 5px 17px 0 rgba(0, 0, 0, 0.05);

                .test-runs-text {
                    font-size: 12px;
                    min-width: 75px;
                }

                .progress-icon {
                    min-width: 75px;
                    text-align: center;
                    margin-left: 0px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }
            }

            .creation-date {
                display: grid;
                text-align: center;
                min-width: 115px;
            }

            .due-date {
                display: grid;
                text-align: center;
                min-width: 120px;
            }

            .hover-row-edit {
                min-width: 120px;

                span {
                    margin-left: 15px;

                    &:first-child {
                        margin-left: 0px;
                    }
                }
            }


        }
    }

}

.status-count {
    font-size: rem(10);

}

.report-table-container {
    overflow-y: auto;
}
.report-table {
    width: 100%;

    th {
        font-size: rem(12);
        color: $grey-200;
        font-weight: 400;
        white-space: nowrap;
        padding-bottom: rem(5);
    }
    thead {
        border-bottom: rem(1) solid $grey-light-200;
        padding-bottom: rem(10);
    }
    td {
        font-size: rem(14);
        white-space: nowrap;
        padding: rem(10) 0;
        padding-right: rem(5);
    }
    .report-name {
        font-size: rem(16);
        display: block;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        max-width: rem(140);
        i {
            font-size: rem(30);
            color: $grey-200;
            transform: scaleX(-1);
            margin-right: rem(10);
        }
        span {
            vertical-align: super;
        }
    }
}

.report-table-btn {
    .btn {
        padding-top: rem(15);
        padding-bottom: rem(15);
    }
}

.milestone-summary {
    height: rem(345);
}

.milestone-chart {
    width: 100%;

    .chart-heading {
        h1 {
            font-size: rem(40);
        }
    }
    @include mq('xxl', min) {
        width: 90%;
        .chart-heading {
            h1 {
                font-size: rem(55);
            }
        }
    }

}

.due-date {
    img {
        height: rem(80);
    }
}

.timestamp {
    font-size: rem(14);
    display: flex;
    flex-direction: column;
    align-items: center;
    font-weight: 600;
}

.sub-milestones-sort {
    height: rem(56);
    border-radius: rem(30);
    width: rem(354);
    .btn {
        height: 100%;
        border-radius: rem(30);
    }
}

.dropmenu {
    display: inline-block;
    .fa-angle-down {
        font-size: 13px;
    }
    .dropdown-menu {
        margin-right: 0;
       
    }
    i {
        color: $grey-300;
    }
    &:hover{
        i {
            color: black ;
        }
    }
}

.bg-progress {
    background-image: url(../images/inprograss.svg);
    background-repeat: no-repeat;
    background-position: center rem(20);
}
.bg-late {
    background-image: url(../images/late.svg);
    background-repeat: no-repeat;
    background-position: center rem(20);
}
.bg-done {
    background-image: url(../images/done.svg);
    background-repeat: no-repeat;
    background-position: center rem(20);
}
.gridboxes__days {
    height: rem(130);

    figure{
 
        padding:0; 
        
        img{
            border-radius:80px;
            width: 117px;
            height:117px;
        }
    }

    .assignedto{
        h5{
            font-size: 12px;
            color: #969ba0;
            margin: 0;
            padding: 0;
        }

        h3{
            font-size: 16px;
            color: #000;
            margin: 0;
            padding: 0;
            font-weight: bold;
        }
    }

    .completed-icon{

        margin-top:20px;

        .icon-complete-tick {
            color: #24cc09;
            font-size: 25px;
        }
    }
}